const next = document.getElementById('arrow-fon-next'), ctxN = next.getContext('2d');
ctxN.beginPath();
ctxN.moveTo(0, 150);
ctxN.quadraticCurveTo(0, 30, 240, 0);
ctxN.lineTo(240, 295);
ctxN.quadraticCurveTo(0, 265, 0, 150);
ctxN.fillStyle = '#E9EBF7';
ctxN.strokeStyle = '#E9EBF7';
ctxN.fill();
ctxN.stroke();

const prev = document.getElementById('arrow-fon-prev'), ctxP = prev.getContext('2d');
ctxP.beginPath();
ctxP.moveTo(0, 295);
ctxP.lineTo(0, 0);
ctxP.quadraticCurveTo(240, 30, 240, 150);
ctxP.quadraticCurveTo(240, 265, 0, 295);

ctxP.fillStyle = '#E9EBF7';
ctxP.strokeStyle = '#E9EBF7';
ctxP.fill();
ctxP.stroke();
