const tab = [...document.getElementsByClassName('case-menu__link')]
const tabContent = [...document.getElementsByClassName('case__item')]


function onClick(e) {
    tab.filter(element => element.closest('.case-menu__link_active')).map(element => element.classList.remove('case-menu__link_active'))
    tabContent.filter(element => element.closest('.case__item_active')).map(element => element.classList.remove('case__item_active'))

    const link = e.target
    link.classList.toggle('case-menu__link_active')
    const index = tab.findIndex(element => element === link)
    tabContent[index].classList.toggle('case__item_active')
    e.preventDefault()
}

tab.forEach(element => {
    element.addEventListener('click', onClick)
})